header {
  width: 100%;
}
header nav {
  padding-top: 12px;
  padding-bottom: 30px;
}
header nav ul {
  display: flex;
  align-items: center;
  justify-content: flex-end;
}
@media (max-width: 767px) {
  header nav ul {
    flex-direction: column;
  }
}
header nav ul li {
  margin: 0px 15px;
}
@media (max-width: 991px) {
  header nav ul li {
    margin: 0px 5px;
  }
}
header nav ul li a {
  font-size: 16px;
  font-weight: 500;
  text-transform: uppercase;
  color: black;
  transition: color 500ms;
}
header nav ul li a:hover {
  color: #ffa41c;
}
@media (max-width: 991px) {
  header nav ul li a {
    font-size: 14px;
  }
}
header nav .menu {
  display: block;
}
@media (max-width: 767px) {
  header nav .menu {
    display: none;
  }
}
header nav .bars {
  border: 2px solid #ffa41c;
  padding: 6px 10px;
  color: black;
  background: transparent;
  font-size: 16px;
  font-weight: 700;
  border-radius: 5px;
  margin: 0px 15px;
  display: none;
}
@media (max-width: 767px) {
  header nav .bars {
    display: block;
  }
}
header .carousel {
  width: 100%;
  height: 600px;
  margin: auto;
  position: relative;
  overflow: hidden;
}
@media (max-width: 767px) {
  header .carousel {
    display: none;
  }
}
@media (max-width: 1199px) {
  header .carousel {
    height: 420px;
  }
}
header .carousel .carousel-img {
  display: flex;
  width: 100%;
  height: 100%;
}
header .carousel .carousel-img .carousel-img-box {
  width: 100%;
  height: 100%;
  position: absolute;
  top: 0;
  left: 0;
  opacity: 0;
  transition: opacity 700ms;
}
header .carousel .carousel-img .carousel-img-box img {
  width: 100%;
}
header .carousel .carousel-img .carousel-img-box .text {
  position: absolute;
  top: 50%;
  left: 50%;
  background: rgba(0, 0, 0, 0.1);
  padding: 50px 100px;
  transform: translate(-50%, -50%);
}
@media (max-width: 991px) {
  header .carousel .carousel-img .carousel-img-box .text {
    padding: 15px 0px;
    top: 35%;
  }
}
header .carousel .carousel-img .carousel-img-box .text h1 {
  font-size: 50px;
  font-weight: 900;
  letter-spacing: 5px;
  color: white;
  text-align: center;
}
@media (max-width: 1199px) {
  header .carousel .carousel-img .carousel-img-box .text h1 {
    font-size: 40px;
  }
}
header .carousel .carousel-img .carousel-img-box .text h1 span {
  display: block;
  font-size: 70px;
  font-weight: 900;
  color: #ffa41c;
}
header .carousel .button {
  position: absolute;
  bottom: 10%;
  width: 100%;
  text-align: center;
}
header .carousel .btn {
  border: 2px solid #ffa41c;
  border-radius: 5px;
  padding: 10px 20px;
  color: #ffa41c;
  background-color: black;
  text-transform: uppercase;
  font-size: 14px;
  font-weight: 700;
  opacity: 0.5;
  transition: opacity 500ms;
}
header .carousel .btn:hover {
  opacity: 1;
}
section .award {
  margin-top: 100px;
}
@media (max-width: 991px) {
  section .award {
    margin-top: 50px;
  }
}
@media (max-width: 767px) {
  section .award {
    margin-top: 20px;
  }
}
section .award .img-card img {
  border-radius: 5px;
}
section .award .text-card {
  padding: 0px 10px;
  margin-bottom: 50px;
}
section .award .text-card h3 {
  margin-top: 20px;
  margin-bottom: 10px;
  font-weight: 700;
  font-size: 16px;
  text-align: center;
}
section .award .text-card p {
  font-size: 14px;
  font-weight: 100;
  line-height: 20px;
}
section .activites {
  width: 100%;
  margin-top: 100px;
  position: relative;
  overflow: hidden;
}
@media (max-width: 991px) {
  section .activites {
    margin-top: 50px;
  }
}
@media (max-width: 767px) {
  section .activites {
    margin-top: 20px;
  }
}
section .activites .activetes-wrap {
  display: flex;
}
@media (max-width: 991px) {
  section .activites .activetes-wrap {
    flex-wrap: wrap;
  }
}
@media (max-width: 991px) {
  section .activites .activetes-wrap {
    justify-content: center;
  }
}
section .activites .activetes-wrap .box {
  width: 100%;
  object-fit: cover;
  position: relative;
}
@media (max-width: 991px) {
  section .activites .activetes-wrap .box {
    width: calc(100% / 12 * 5);
    margin: 30px 10px;
  }
}
@media (max-width: 767px) {
  section .activites .activetes-wrap .box {
    width: calc(100% / 12 * 12);
    margin: 30px 10px;
  }
}
section .activites .activetes-wrap .box img {
  max-width: 100%;
  width: 100%;
  height: 500px;
}
section .activites .activetes-wrap .box .big {
  display: none;
  height: 510px;
}
section .activites .activetes-wrap .box .activities-text {
  width: 100%;
  position: absolute;
  left: 0;
  bottom: 0%;
  padding-left: 20px;
  padding-bottom: 35px;
  background: rgba(0, 0, 0, 0.2);
}
@media (max-width: 991px) {
  section .activites .activetes-wrap .box .activities-text {
    background: transparent;
  }
}
section .activites .activetes-wrap .box .activities-text h3 {
  font-size: 40px;
  font-weight: 700;
  color: white;
  text-transform: uppercase;
  margin-bottom: 10px;
}
@media (max-width: 1199px) {
  section .activites .activetes-wrap .box .activities-text h3 {
    font-size: 30px;
  }
}
section .activites .activetes-wrap .box .activities-text:hover h3 {
  transition: color 500ms;
  cursor: pointer;
  color: #ffa41c;
}
section .activites .activetes-wrap .box .activities-text p {
  font-size: 16px;
  font-weight: 100;
  color: white;
}
section .rooms {
  margin-top: 100px;
}
@media (max-width: 991px) {
  section .rooms {
    margin-top: 50px;
  }
}
@media (max-width: 767px) {
  section .rooms {
    margin-top: 20px;
  }
}
@media (max-width: 991px) {
  section .rooms .box-room .room-img {
    margin: 0px 10px;
  }
}
@media (max-width: 991px) {
  section .rooms .box-room .room-text {
    margin: 0px 10px;
  }
}
section .rooms .box-room .room-text h3 {
  font-size: 16px;
  font-weight: 700;
  color: black;
  margin: 10px 0px;
}
section .rooms .box-room .room-text p {
  font-size: 16px;
  font-weight: 100;
  line-height: 30px;
}
section .weather {
  width: 100%;
  height: 550px;
  background-attachment: fixed;
  margin-top: 100px;
  background: url("../img/bgwinter_02.png") no-repeat center center / cover;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
}
@media (max-width: 991px) {
  section .weather {
    margin-top: 50px;
  }
}
@media (max-width: 767px) {
  section .weather {
    margin-top: 20px;
  }
}
section .weather h2 {
  color: #ffa41c;
  text-shadow: 2px 2px 2px black;
  text-transform: uppercase;
  font-size: 70px;
  font-weight: 700;
  letter-spacing: 5px;
}
@media (max-width: 767px) {
  section .weather h2 {
    font-size: 30px;
  }
}
section .weather p {
  font-size: 16px;
  color: #ffa41c;
  text-shadow: 2px 2px 2px black;
  font-weight: 100;
  text-align: center;
  line-height: 30px;
}
section .weather .call {
  display: block;
  margin-top: 10px;
  padding: 12px 50px;
  color: white;
  font-size: 14px;
  font-weight: 700;
  border: 4px solid #ffa41c;
  border-radius: 5px;
  background: transparent;
  letter-spacing: 5px;
  transition: color 500ms;
  color: black;
}
section .weather .call:hover {
  color: #ffa41c;
}
footer {
  width: 100%;
  background: #252525;
  padding: 40px 0px;
  margin-top: 100px;
}
footer p {
  color: white;
  font-size: 18px;
  font-weight: 700;
  margin-bottom: 15px;
  text-transform: uppercase;
}
footer p::after {
  content: "";
  width: 20%;
  height: 4px;
  background: #ffa41c;
  display: block;
  margin: 5px 0px;
}
footer ul li {
  margin: 10px 0px;
}
footer ul li a {
  color: white;
  font-size: 14px;
  display: block;
  font-weight: 500;
  transition: color 500ms;
}
footer ul li a:hover {
  color: #ffa41c;
}
footer .social {
  display: flex;
  justify-content: center;
}
footer .social li {
  margin: 0px 15px;
}
footer .social li a {
  font-size: 16px;
  font-weight: 700;
}
* {
  margin: 0;
  padding: 0;
  text-decoration: none;
  list-style: none;
  box-sizing: border-box;
}
img {
  width: 100%;
  max-width: 100%;
  height: auto;
}
body {
  min-width: 320px;
  font-family: "etelkReg";
  text-decoration: none;
  position: relative;
}
h2 {
  font-size: 50px;
  font-weight: 700;
  color: black;
  text-transform: uppercase;
  margin-bottom: 30px;
  text-align: center;
  letter-spacing: 3px;
}
@media (max-width: 991px) {
  h2 {
    font-size: 30px;
  }
}
.active-cl {
  opacity: 1 !important;
}
.active-winter {
  position: absolute !important;
  bottom: 0;
  left: 0;
  transition: 500ms;
  width: 100% !important;
  z-index: 111;
}
.mb_10 {
  margin-bottom: 20px;
}
@media (max-width: 991px) {
  .md-mb-10 {
    margin-bottom: 20px;
  }
}
