section {
  .award {
    margin-top: 100px;
    .md(margin-top , 50px);
    .sm(margin-top, 20px);
    .img-card {
      img {
        border-radius: 5px;
      }
    }
    .text-card {
      padding: 0px 10px;
      margin-bottom: 50px;
      h3 {
        margin-top: 20px;
        margin-bottom: 10px;
        font-weight: 700;
        font-size: 16px;
        text-align: center;
      }
      p {
        font-size: 14px;
        font-weight: 100;
        line-height: 20px;
      }
    }
  }
  .activites {
    width: 100%;
    
    margin-top: 100px;
    .md(margin-top , 50px);
    .sm(margin-top, 20px);
    position: relative;
    overflow: hidden;
    .activetes-wrap {
      display: flex;
     .md(flex-wrap , wrap);
     .md(justify-content , center);
      .box {
        width: 100%;
        .md-block({
            width: calc(100% / 12 * 5);
            margin: 30px 10px;
        });
        .sm-block({
            width: calc(100% / 12 * 12);
            margin: 30px 10px;
        });
        object-fit: cover;
        position: relative;
        

        img {
          max-width: 100%;
          width: 100%;
          height: 500px;
        }
        .big {
          display: none;
          height: 510px;
        }
        .activities-text {
          width: 100%;
          position: absolute;
          left: 0;
          bottom: 0%;
          padding-left: 20px;
          padding-bottom: 35px;
          background: rgba(0, 0, 0, 0.2);
          .md(background , transparent);
          h3 {
            font-size: 40px;
            font-weight: 700;
            color: white;
            text-transform: uppercase;
            margin-bottom: 10px;
            .lg(font-size , 30px);
          }
          &:hover h3 {
            transition: color 500ms;
            cursor: pointer;
            color: #ffa41c;
          }
          p {
            font-size: 16px;
            font-weight: 100;
            color: white;
          }
        }
      }
    }
  }
  .rooms {
      margin-top: 100px;
    
      .md(margin-top , 50px);
      .sm(margin-top, 20px);
    .box-room {
        .room-img{
            .md-block({
                margin: 0px 10px;
            });
        };
      .room-text {
        .md-block({
            margin: 0px 10px;
        });
        h3 {
          font-size: 16px;
          font-weight: 700;
          color: black;
          margin: 10px 0px;
        }
        p {
          font-size: 16px;
          font-weight: 100;
          line-height: 30px;
        }
      }
    }
  }
  .weather {
    width: 100%;
    height: 550px;
    background-attachment: fixed;
    margin-top: 100px;
     
    .md(margin-top , 50px);
    .sm(margin-top, 20px);
    background: url("../img/bgwinter_02.png") no-repeat center center / cover;
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    h2 {
      color: rgb(255, 164, 28);
      text-shadow: 2px 2px 2px black;
      text-transform: uppercase;
      font-size: 70px;
      font-weight: 700;
      letter-spacing: 5px;
      .sm(font-size , 30px);
    }
    p {
      font-size: 16px;
      color: rgb(255, 164, 28);
      text-shadow: 2px 2px 2px black;
      font-weight: 100;
      text-align: center;
      line-height: 30px;
    }
    .call {
      display: block;
      margin-top: 10px;
      padding: 12px 50px;
      color: white;
      font-size: 14px;
      font-weight: 700;
      border: 4px solid rgb(255, 164, 28);
      border-radius: 5px;
      background: transparent;
      letter-spacing: 5px;
      transition: color 500ms;
      color: black;
      &:hover {
        color: #ffa41c;
      }
    }
  }
}
