footer {
  width: 100%;
  background: #252525;
  padding: 40px 0px;
  margin-top: 100px;
  p {
    color: white;
    font-size: 18px;
    font-weight: 700;
    margin-bottom: 15px;
    text-transform: uppercase;
    
    &::after{
        content: "";
        width: 20%;
        height: 4px;
        background:  #ffa41c;
        display: block;
        margin: 5px 0px;
    }
  }
  ul {
    li {
      margin: 10px 0px;
      a {
        color: white;
        font-size: 14px;
        display: block;
        font-weight: 500;
        transition: color 500ms;
        &:hover{
            color: #ffa41c;
        }
      }
    }
  }
  .social {
    display: flex;
    justify-content: center;
    li {
      margin: 0px 15px;
      a{
          font-size: 16px;
          font-weight: 700;

      }
    }
  }
}
