@import "./params/mix";
@import "./header";
@import "./section";
@import "./footer";
 

*{
  margin: 0;
  padding: 0;
  text-decoration: none;
  list-style: none;
  box-sizing: border-box;
}

img {
  width: 100%;
  max-width: 100%;
  height: auto;
}

body {
  min-width: 320px;
  font-family: "etelkReg";
  text-decoration: none;
  position: relative;
}

h2{
  font-size: 50px;
  font-weight: 700;
  color: black;
  text-transform: uppercase;
  margin-bottom: 30px;
  text-align: center;
  letter-spacing: 3px;
 .md-block({
   font-size: 30px;
 })
}

.active-cl {
  opacity: 1 !important;
}

.active-winter{
  position: absolute !important;
  bottom: 0;
  left: 0;
  transition: 500ms;
  width: 100% !important;
  z-index: 111;
}

.mb_10{
  margin-bottom: 20px;
}

.md-mb-10{
  .md( margin-bottom , 20px);
}
/* System GRID */

.paper {
  width: 100%;
  max-width: 1140px;
  margin: auto;
}

.paper-wrap {
  display: flex;
  flex-wrap: wrap;
}

.box-1 {
  width: calc(100% / 12 * 1);
  padding: 0px 15px;
}
.box-2 {
  width: calc(100% / 12 * 2);
  padding: 0px 15px;
}
.box-3 {
  width: calc(100% / 12 * 3);
  padding: 0px 15px;
}
.box-4 {
  width: calc(100% / 12 * 4);
  padding: 0px 15px;
}
.box-5 {
  width: calc(100% / 12 * 5);
  padding: 0px 15px;
}
.box-6 {
  width: calc(100% / 12 * 6);
  padding: 0px 15px;
}
.box-7 {
  width: calc(100% / 12 * 7);
  padding: 0px 15px;
}
.box-8 {
  width: calc(100% / 12 * 8);
  padding: 0px 15px;
}
.box-9 {
  width: calc(100% / 12 * 9);
  padding: 0px 15px;
}
.box-10 {
  width: calc(100% / 12 * 10);
  padding: 0px 15px;
}
.box-11 {
  width: calc(100% / 12 * 11);
  padding: 0px 15px;
}
.box-12 {
  width: calc(100% / 12 * 12);
  padding: 0px 15px;
}

 .md-box-6{
   .md-block({
    width: calc(100% / 12 * 6);
    padding: 0px 15px;
   })
 }
 .md-box-12{
   .md-block({
    width: calc(100% / 12 * 12);
    padding: 0px 15px;
   })
 }

 .md-d-flex{
  .md-block({
    display: flex;
   })
 }
 .md-d-block{
  .md-block({
    display: block !important;
   })
 }
 .sm-d-block{
  .sm-block({
    display: block !important;
   })
 }
 .md-d-none{
  .md-block({
    display: none;
   })
 }
 .md-justify-between{
  .md-block({
    justify-content: space-between;
   })
 }

 .sm-box-12{
  .sm-block({
    width: calc(100% / 12 * 12);
    padding: 0px 15px;
   })
 }

 .sm-box-6{
  .sm-block({
    width: calc(100% / 12 * 6);
    padding: 0px 15px;
   })
 }
 .sm-box-4{
  .sm-block({
    width: calc(100% / 12 * 4);
    padding: 0px 15px;
   })
 }



.d-flex {
  display: flex;
}

.justify-center {
  justify-content: center;
}

.justify-between {
  justify-content: space-between;
}

.align-center {
  align-items: center;
}

