header {
  width: 100%;
  nav {
    padding-top: 12px;
    padding-bottom: 30px;
    ul {
      display: flex;
      align-items: center;
      justify-content: flex-end;
      .sm-block({
        flex-direction: column;
      });
      li {
        margin: 0px 15px;
        .md-block({
          margin: 0px 5px;
        });
        a {
          font-size: 16px;
          font-weight: 500;
          text-transform: uppercase;
          color: black;
          transition: color 500ms;
          &:hover {
            color: rgb(255, 164, 28);
          }
          .md-block({
            font-size: 14px;
          });
        }
      }
    }
    .menu {
      display: block;
     .sm( display, none);
    }
    .bars {
      border: 2px solid rgb(255, 164, 28);
      padding: 6px 10px;
      color: black;
      background: transparent;
      font-size: 16px;
      font-weight: 700;
      border-radius: 5px;
      margin: 0px 15px;
      display: none;
      .sm(display , block);
    }
  }
  .carousel {
    .sm(display , none);
    width: 100%;
    height: 600px;
    margin: auto;
    position: relative;
    overflow: hidden;
    .lg(height , 420px);
    .carousel-img {
      display: flex;
      width: 100%;
      height: 100%;
      .carousel-img-box {
        width: 100%;
        height: 100%;
        position: absolute;
        top: 0;
        left: 0;
        opacity: 0;
        transition: opacity 700ms;
        img {
          width: 100%;
        }
        .text {
          position: absolute;
          top: 50%;
          left: 50%;
          background: rgba(0, 0, 0, 0.1);
          padding: 50px 100px;
          transform: translate(-50%, -50%);
          .md-block({
            padding: 15px 0px;
            top: 35%;
          });
          h1 {
            font-size: 50px;
            font-weight: 900;
            letter-spacing: 5px;
            color: white;
            text-align: center;
            .lg(font-size , 40px);
            span {
              display: block;
              font-size: 70px;
              font-weight: 900;
              color: rgb(255, 164, 28);
            }
          }
        }
      }
    }
    .button {
      position: absolute;
      bottom: 10%;
      width: 100%;
      text-align: center;
    }

    .btn {
      border: 2px solid rgb(255, 164, 28);
      border-radius: 5px;
      padding: 10px 20px;
      color: rgb(255, 164, 28);
      background-color: black;
      text-transform: uppercase;
      font-size: 14px;
      font-weight: 700;
      opacity: 0.5;
      transition: opacity 500ms;
      &:hover {
        opacity: 1;
      }
    }
  }
}
